body{
    background-color: #1f1e1e;
    color: white;
    overflow: hidden;
  }
  
  h1{
    text-align: center;
    font-size: 3rem;
    color: white;
  }
  
  
  .todo-wrapper{
    background-color: #353434;
    padding: 2%;
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
    margin-top: 3%;
    max-height: 80vh;
    overflow-y: auto;
    box-shadow: 0px 5px 7px rgb(26, 25, 25);
   
    
  }
  
  .todo-input{
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid rgb(77, 77, 77);
    padding-bottom: 25px;
    margin-bottom: 25px;
    
  }
  
  .todo-input-item{
    margin-right: 25px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    
  }
  
  .todo-input-item label{
    margin-bottom: 10px;
    font-weight: bold;
  }
  
  .todo-input-item input {
    border: none;
    padding: 8px;
    width: 100%;
  }
  
  .todo-input-item input:focus{
    outline: 2px solid rgb(0, 230, 122);
  }
  
  
  
  
  .primary-btn{
    background-color: rgb(0, 230, 122);
    color: white;
    border-radius: 0px;
    border: none;
    margin-top: 25px;
    padding: 10px;
    width: 100%;
    cursor: pointer;
  }
  
  .primary-btn:hover{
    background-color: rgb(4, 192, 104);
  }
  
  .btn-area{
    margin-bottom: 10px;
    text-align: justify;
  }
  
  
  .secondaryBtn{
    background-color: rgb(92, 92, 92);
    color: white;
    border-radius: 0px;
    border: none;
    padding: 10px;
    width: fit-content;
    cursor: pointer;
  }
  
  .btn-area .active{
    background-color: rgb(4, 192, 104);
  }
  
  
  
  .todo-list{
    display: flex;
    flex-direction: column;
  }
  
  .todo-list-item{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #414040;
    padding: 25px;
    padding-top: 10px;
    padding-bottom: 10px;
    margin-bottom: 10px;
    box-shadow: 0px 2px 4px rgb(39, 38, 38);
  }
  
  .todo-list-item h3{
    font-size: 1.5rem;
    color: rgb(31, 218, 131);
    font-weight: bold;
    margin: 0px;
  }
  
  .todo-list-item p{
    margin-top: 8px;
    font-size: 1rem;
    color: rgb(179, 178, 178);
  
  }
  
  .icon{
    font-size: 2rem;
    cursor: pointer;
  }
  
  .icon:hover{
    color: red;
  
  }
  
  .check-icon{
    font-size: 22px;
    cursor: pointer;
    color: rgb(0, 230, 122);
    margin-left: 10px;
    margin-bottom: 5px;
  }
  
  .check-icon:hover{
    color: rgb(4, 192, 104);
  }